.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 30vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.counter {
  margin: 50px auto;
  width: 50%;
  padding: 20px;
  border: 1px solid blue;
}
h2 {
  font-weight: bold;
}
span {
  color: red;
}
button {
  background-color: blue;
  color: white;
  border: none;
  margin: 10px;
  padding: 5px;
}
.step {
  display: inline-block;
  margin: 0 20px 0 0;
  color: blue;
  font-size: 20px;
}